import React, { useState, useEffect } from "react";

import "./App.css";

export default function App() {
	const [todoList, setTodoList] = useState([]);

	useEffect(() => {
		fetch("http://127.0.0.1:8000/todos/")
			.then(res => {
				setTodoList(res.json().todo);
			})
			.catch(err => {
				console.log(err);
			});
	}, []);

	const handleRemove = id => {
		console.log(id);
	};

	return (
		<div className="app">
			<div className="todo">
				<h1 className="todo__title">TODO App</h1>
				<h2 className="todo__sub-title">Do it now.</h2>

				<AddTask />

				<table className="todo__todo-table">
					<thead className="todo-table__header">
						<tr>
							<th>#</th>
							<th>Task Name</th>
							<th>Status</th>
							<th>Remove</th>
						</tr>
					</thead>

					<tbody className="todo-table__content">
						{todoList.map(el => (
							<tr className="content__item">
								<td>{el.id}</td>
								<td>{el.title}</td>
								<td>
									<Status initStatusId={el.status} />
								</td>
								<td>
									<button
										className="item__remove-btn"
										type="button"
										onClick={() => handleRemove(el.id)}>
										Remove
									</button>
								</td>
							</tr>
						))}
					</tbody>
				</table>
				{todoList.length === 0 ? (
					<div
						style={{
							width: "100%",
							height: "100%",
							display: "flex",
							justifyContent: "center",
							alignItems: "center",
							background: "#eee",
						}}>
						Empty
					</div>
				) : null}
			</div>
		</div>
	);
}

function AddTask() {
	const [addTaskFormVisible, setAddTaskFormVisible] = useState(false);

	const [newTaskData, setNewTaskData] = useState({
		title: "",
		status: "1",
	});

	const handleAddTask = () => {
		console.log(newTaskData);
	};

	return (
		<>
			<div
				className="add-task"
				onClick={() => setAddTaskFormVisible(!addTaskFormVisible)}>
				Add Task
			</div>
			<div
				className="add-task__form"
				style={{ display: addTaskFormVisible ? "flex" : "none" }}>
				<p>
					Task name:{" "}
					<input
						type="text"
						value={newTaskData.title}
						onChange={({ target: { value } }) =>
							setNewTaskData({ ...newTaskData, title: value })
						}
					/>
				</p>

				<p>
					Status:{" "}
					<select
						value={newTaskData.status}
						onChange={({ target: { value } }) =>
							setNewTaskData({ ...newTaskData, status: value })
						}>
						<option value="1">ToDo</option>
						<option value="2">InProgress</option>
						<option value="3">Complete</option>
					</select>
				</p>

				<hr />

				<button className="create-btn" onClick={handleAddTask}>
					Create
				</button>
				<button
					className="cancel-btn"
					onClick={() => setAddTaskFormVisible(false)}>
					Cancel
				</button>
			</div>
		</>
	);
}

function Status({ initStatusId }) {
	const statusList = [
		{ title: "ToDo", color: "blue" },
		{ title: "InProgress", color: "orange" },
		{ title: "Complete", color: "green" },
	];

	const [activeStatusIndex, setActiveStatusIndex] = useState(initStatusId - 1);

	const changeStatus = () => {
		if (activeStatusIndex === statusList.length - 1) {
			setActiveStatusIndex(0);
		} else {
			setActiveStatusIndex(activeStatusIndex + 1);
		}
	};

	return (
		<div
			className="status"
			style={{ color: statusList[activeStatusIndex].color }}
			onClick={changeStatus}>
			{statusList[activeStatusIndex].title}
		</div>
	);
}
